import React from 'react';
import getMovieData from '../../services/getMovieData';
import '../searchMovie/searchMovie.scss';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import YearPicker from "react-year-picker";

class searchMovie extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            movieData: [],
            search: true,
            inputVal: "",
            norecords: false,
            selectGenre: "",
            selectRating: "",
            snackBarText: "",
            loader: false
        }
        this.getMovieData = this.getMovieData.bind(this);    
        this.handleGenre = this.handleGenre.bind(this);
        this.handleRating = this.handleRating.bind(this);
        this.removeFooter = this.removeFooter.bind(this);
    }


    getMovieData(data){
    if(this.state.selectGenre !== "" || this.state.selectRating !== ""){
        var genreArray = [];
        var ratingArray = [];
        var medium = [];
        var ratingFlag = false;
        var genreFlag = false;

        // Genre Filter
        if(this.state.selectGenre !== ""){
        data.results.filter((ele)=>{
            ele.genre_ids.filter((ele2)=>{
                if(ele2 === parseInt(this.state.selectGenre)){
                    genreArray.push(ele)
                }
                return 0;
            })
            return 0;
        });
        genreFlag = true;
        }
        // Rating Filter
        if(this.state.selectRating !== ""){
        if(genreFlag === true){
            medium = genreArray;
        }else{
            medium = data.results;
        }
        
        medium.filter((ele)=>{
            if(Math.floor(ele.vote_average) === parseInt(this.state.selectRating)){
                ratingArray.push(ele)
            }
            return 0;
        });
        ratingFlag = true;
        }
        // Final storage of both filter
        if(ratingFlag === true){
        this.setState({
            movieData: ratingArray
        });
        }else{
        this.setState({
            movieData: genreArray
        });
        }
    }else{
        this.setState({
            movieData: data.results
        });
    }
    // Disable Loader
    this.setState({
        loader: false
    })
    // No Records
    if(this.state.movieData.length === 0){
        this.setState({
            norecords: true
        })
        this.props.setNPIcon(false)
    }else{
        this.setState({
            norecords: false
        })
        this.props.setNPIcon(true)
    }
    }

    updateInputVal(evt){
    this.setState({
        inputVal: evt.target.value
    })
    evt.preventDefault();
    }

    passID(ID){
        this.props.setMovieID(ID)
    }

    searchMovie(e){
    // Getting year from input text
    var year = document.getElementsByClassName("year-input")[0].defaultValue;
    if(this.state.inputVal!==""){
        if((parseInt(year)>=1583 && parseInt(year)<=9998) || year === ""){
            this.setState({
                loader: true
            });
            getMovieData.searchMovie(this.getMovieData,this.state.inputVal,year);  
        }else{
        this.setState({
            snackBarText: "Please Enter valid year"
        })
        this.toggleSnackBar();
        }
    }else{
        this.setState({
        snackBarText: "Movie Field must be filled out"
        })
        this.toggleSnackBar();
    }
    e.preventDefault();
    }

    handleGenre(e){
        this.setState({selectGenre:e.target.value});
        // console.log(e.target.value); 
    }
    
    handleRating(e){
        this.setState({selectRating:e.target.value});
        // console.log(e.target.value);
    }
    
    removeFooter(){
        setTimeout(()=>{
            var el = document.getElementsByClassName("footer");
            if(el[0]){
                el[0].parentElement.removeChild(el[0])    
            }
        },0.1)
    }
    
    toggleSnackBar() {
        var x = document.getElementById("snackbar");
        x.className = "show";
        setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
    }

  render(){    
    return (
      <div id="home-container">

        <div id="snackbar">{this.state.snackBarText}</div>

        <div className="title">
            <p className="filter-desc">Filter search results based on user input</p>
        </div>

        <div className="filter-section">
            <div className="section-container">
                <label>Genre:</label>
                <select name="genre" value={this.state.selectGenre} onChange={evt => this.handleGenre(evt)}>
                    <option value="">All</option>
                    <option value="28">Action</option>
                    <option value="12">Adventure</option>
                    <option value="16">Animation</option>
                    <option value="35">Comedy</option>
                    <option value="80">Crime</option>
                    <option value="99">Documentary</option>
                    <option value="18">Drama</option>
                    <option value="10751">Family</option>
                    <option value="14">Fantasy</option>
                    <option value="36">History</option>
                    <option value="27">Horror</option>
                    <option value="10402">Music</option>
                    <option value="9648">Mystery</option>
                    <option value="10749">TV Movie</option>
                    <option value="romance">Romance</option>
                    <option value="878">Sci-Fi</option>
                    <option value="53">Thriller</option>
                    <option value="10752">War</option>
                    <option value="37">Western</option>
                </select>
            </div>

            <div className="section-container">
                <label>Rating:</label>
                <select name="rating" value={this.state.selectRating} onChange={evt => this.handleRating(evt)}>
                    <option value="">All</option>
                    <option value="10">10+</option>
                    <option value="9">9+</option>
                    <option value="8">8+</option>
                    <option value="7">7+</option>
                    <option value="6">6+</option>
                    <option value="5">5+</option>
                    <option value="4">4+</option>
                    <option value="3">3+</option>
                    <option value="2">2+</option>
                    <option value="1">1+</option>
                </select>
            </div>

            <div className="section-container">
                <label>Year:</label>
                <div onClick={ () => this.removeFooter()}>
                    <YearPicker/>
                </div>
            </div>
        </div>

        <form onSubmit={e => this.searchMovie(e)}>
            <input placeholder="search movies" type="text" className="search-bar" value={this.state.inputVal} onChange={evt => this.updateInputVal(evt)}/>
            <i className="fas fa-search" onClick={e => this.searchMovie(e)}></i>
        </form>

        {/* Loader */}
        { 
            this.state.loader &&
            <div className="loaderRoot">
            <div className="showbox">
                <div className="loader">
                <svg className="circular" viewBox="25 25 50 50">
                    <circle className="path" cx="50" cy="50" r="20" fill="none" strokeWidth="2" strokeMiterlimit="10"></circle>
                </svg>
                </div>
            </div>
            </div>
        }

        {/* No Movie */}
        <div className={"no-record "+ (this.state.norecords ? "no-record-true": "") }>
            <img src={require('../../assets/no-record.png')} alt="no-record"/>
            <p>No Movies found</p>
        </div>

        <div className="card-container">
            {
            this.state.movieData &&

                this.state.movieData.map((d,k) => {
                    var imgPath = 'https://image.tmdb.org/t/p/w500/'+d.poster_path;
                    // var bgImg = 'https://image.tmdb.org/t/p/w500/'+d.backdrop_path;
                    var year = d.release_date.split("-")[0];

                return <div id={d.id} key={k} className="card">
                    <div className="movie-desc">
                        <i className="fas fa-star"></i>
                        <p className="rating">{d.vote_average} / 10</p>
                        <div>
                        <Link onClick={()=>this.passID(d.id)} to="/movie-detail">View Details</Link>
                        </div>
                    </div>
                    <img id="select" src={imgPath} alt="movie-img" onError={(e)=>{e.target.onerror = null; e.target.src=require('../../assets/movie_placeholder.png')}}/>
                    <p className="movie-title">{d.title}</p>
                    <p className="movie-year">{year}</p>
                    </div>
                })
            }
        </div>

      </div>
    );
  }
}

function mapStateToProps(state){
    return{
      search: state.searchStatus,
      loadHome: state.loadHome
    }
}

function mapDispatchToProps(dispatch){
  return{
      setMovieID :(ID) => dispatch({type:'SET_MOVIE_ID',ID}),
      setNPIcon :(data) => dispatch({type:'SET_NP_ICON',data}),
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(searchMovie);