import React from 'react';
import {Route, Switch,  HashRouter as Router} from 'react-router-dom';
import Home from '../Home/Home';
import MovieDetail from '../MovieDetail/MovieDetail'
import searchMovie from '../searchMovie/searchMovie'

import '../App/App.scss';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';


class App extends React.Component {

  constructor(props){
    super(props);
    this.state ={
      // inputVal: ""
    }
  }

  triggerSearch(){
    this.props.loadHome(true);
  }

  render(){
    var NPstatus = this.props.NPIcon === true ? 'showNP' : '';
    
    return (
      <div className="App-header">
        <Router>
        <div className="navbar">
          <img src={require('../../assets/sathish.png')}  alt="logo"/>

          <div className={"now-playing "+NPstatus}>
            <Link to="/" onClick={e => this.triggerSearch(e)}>
              <i className="fas fa-home"></i>
            </Link>
            {/* <div className="tooltip">Click here for Latest Movies</div> */}
          </div>
          {/* <div className="adv-search-btn" onClick={e => this.triggerSearch(e)}>Advanced Search</div> */}
        </div>
          <Switch>
            <Route exact path="/" component={Home}/>
            <Route exact path="/:movie-detail" component={MovieDetail}/>
            <Route exact path="/:search-movie" component={searchMovie}/>
          </Switch>
        </Router>  
        </div>
    );
  }
}

function mapStateToProps(state){
  return {
      NPIcon : state.NPIcon
  }
}

function mapDispatchToProps(dispatch){
    return{
        setMovieData :(data) => dispatch({type:'SET_MOVIE_DATA',data}),
        searchQuery: (data) => dispatch({type: 'SET_SEARCH_QUERY',data}),
        searchStatus: (data) => dispatch({type: 'SEARCH_STATUS',data}),
        loadHome: (data) => dispatch({type: 'LOAD_HOME',data})
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);

