var initialState = {
    ID : '',
    NPIcon: Boolean,
    searchQuery: "",
    searchStatus: Boolean,
    loadHome: Boolean
}

const reducer =  function (state = initialState, action) {
    switch(action.type){
        case 'SET_MOVIE_ID':{
            state.ID = action.ID;
            return state;
        }
        case 'SET_NP_ICON':{
            state.NPIcon = action.data;
            return state;
        }
        case 'SET_SEARCH_QUERY': {
            state.searchQuery = action.data;
            return state;
        }
        case 'SEARCH_STATUS': {
            state.searchStatus = action.data;
            return state;
        }
        case 'LOAD_HOME': {
            state.loadHome = action.data;
            return state;
        }
        default: return state;
    }
}

export default reducer;