import React, { Component } from 'react';
import getMovieData from '../../services/getMovieData';
import '../MovieDetail/MovieDetail.scss';
import {connect} from 'react-redux';
import ModalVideo from 'react-modal-video'



class MovieDetail extends Component {
  constructor(props){
    super(props);
    
    this.state = {
        movieData: [],
        videoData: [],
        castingData: [],
        personData: [],
        updated: false,
        ID: this.props.ID,
        isOpen: false,
        videoNone: false
    }
    this.getMovie = this.getMovie.bind(this);    
    this.getVideo = this.getVideo.bind(this);  
    this.getCasting = this.getCasting.bind(this);    
    this.getPersonData = this.getPersonData.bind(this);

    this.openModal = this.openModal.bind(this);
  }

  componentDidMount(){
    getMovieData.getMovieDetail(this.getMovie,this.state.ID);
    getMovieData.getVideoDetail(this.getVideo,this.state.ID);
    getMovieData.getCastingDetail(this.getCasting,this.state.ID);
  }

  getMovie(data){
      this.setState({
        movieData: data
      });
  }

  getVideo(data){  
    if(data.length > 0 ){
      this.setState({
        videoKey: data[0].key,
        videoName: data[0].name
      });
    }
  }

  getCasting(data){
    this.setState({
      castingData: data,
      updated: true
    });
  }

  getPerson(ID){
        getMovieData.getPersonDetail(this.getPersonData,ID);    
  }

  getPersonData(data){
    this.setState({
      personData: data
    });
    // console.log(this.state.personData);
  }

  openModal () {
    this.setState({isOpen: true})
  }
  
  render(){
      // console.log("Movie Data");
      // console.log(this.state.movieData);
      // console.log("Video Data");
      // console.log(this.state.videoData);
      // console.log("Casting Data");
      // console.log(this.state.castingData.cast);

      var imgPath = 'https://image.tmdb.org/t/p/w500/'+this.state.movieData.poster_path;
      var bgImg = 'https://image.tmdb.org/t/p/w500'+this.state.movieData.backdrop_path;
      var year = this.state.movieData.release_date ? this.state.movieData.release_date.split("-")[0] : null;
      
      return(
            <div className="movie-container">
                <div className="m-details">
                    {/* Modal Video section */}
                    <ModalVideo channel='youtube' isOpen={this.state.isOpen} videoId={this.state.videoKey} onClose={() => this.setState({isOpen: false})} />

                    {/* Poster section */}
                    <div className="m-container card-container">

                      <div className="card">
                        <img src={imgPath} alt="movie-img" onError={(e)=>{e.target.onerror = null; e.target.src=require('../../assets/movie_placeholder.png')}}/>
                      </div>
                      
                  
                        <div className="movie-detail">
                        
                          { this.state.movieData.original_title &&
                          <div>
                            <h1>{this.state.movieData.original_title}</h1>
                            <p className="tag-name">{this.state.movieData.tagline}</p>
                            <h3>{year}</h3>
                          </div>
                          }
                        
                          { this.state.movieData.genres && 
                          <div className="genre">
                            {
                              this.state.movieData.genres.map((d,k)=>{
                                return <div key={k}>{d.name}</div>
                              })
                            }
                          </div>
                          }
                          
                          { this.state.movieData.runtime &&
                            <p className="run-time">Running time: {this.state.movieData.runtime} mins</p>
                          }

                          { this.state.movieData.spoken_languages  &&
                          <div className="language">
                            Language: 
                          {
                            this.state.movieData.spoken_languages.map((d,k)=>{
                              return <div key={k}>&nbsp;{d.name}</div>
                            })
                          }
                          </div>
                          }
                          
                          {/* { this.state.movieData.vote_count && this.state.movieData.vote_average && */}
                          <div className="ratings">
                              <div><i className="fas fa-heart"></i> {this.state.movieData.vote_count} - Likes</div>
                              <div><i className="fab fa-imdb"></i> {this.state.movieData.vote_average} / 10 - Ratings</div>
                          </div>
                          {/* } */}
                        
                        </div>
                      </div>
                      

                    {/* Video section */}
                    { this.state.videoName && 
                      <div>
                      <h2 className="ytube-title">{this.state.videoName}</h2>
                      <div className="ytube">
                        <i onClick={this.openModal} className="fas fa-play-circle"></i>
                        <img src={bgImg} alt="video"/>
                      </div>
                      </div>
                    }
                      
                    {/* Synopsis & Cast Section */}
                    <div className="synopsis-cast">
                          { this.state.movieData.overview && 
                            <div className="synopsis">
                              <h2>Synopsis:</h2>
                              <p>{this.state.movieData.overview}</p>
                            </div>
                          }

                          { 
                            <div className="cast-container">
                              { this.state.castingData && <h2>Casting:</h2> }
                              { this.state.castingData.cast &&
                                this.state.castingData.cast.map((d,k)=>{
                                  var castImg = 'https://image.tmdb.org/t/p/w500'+d.profile_path;
                                  return <div key={k} className="cast" onClick={()=>this.getPerson(d.id)}>
                                    <img src={castImg} onError={(e)=>{e.target.onerror = null; e.target.src=require('../../assets/placeholder.png')}} alt="cast-img"/>
                                    <p>{d.name}</p>
                                    </div>
                                })
                              }
                            </div>
                          }
                        </div>
                </div>
            </div>
      );
  }
}

function mapStateToProps(state){
    return {
        ID : state.ID
    }
}

// function mapDispatchToProps(dispatch){
//     return{
//         setMovieID :(ID) => dispatch({type:'SET_MOVIE_ID',ID}),
//     }
//   }
  
export default connect(mapStateToProps,null)(MovieDetail);

