import axios from 'axios';

const getMovieData = {
    nowPlaying(callback) {
        axios.get('https://api.themoviedb.org/3/movie/now_playing?api_key=bd4dc10114f188e3f7c600026923eb00')
            .then(res => {
                callback(res.data);
            });
    },
    getMovieDetail(callback,ID){
        axios.get('https://api.themoviedb.org/3/movie/'+ID+'?api_key=bd4dc10114f188e3f7c600026923eb00')
        .then(res => {
            callback(res.data);
        }).catch(function (error) {
            window.location.href = "/";
        })
    },
    getVideoDetail(callback,ID){
        axios.get('https://api.themoviedb.org/3/movie/'+ID+'/videos?api_key=bd4dc10114f188e3f7c600026923eb00')
        .then(res => {            
            callback(res.data.results);
        });
    },
    getCastingDetail(callback,ID){
        axios.get('https://api.themoviedb.org/3/movie/'+ID+'/credits?api_key=bd4dc10114f188e3f7c600026923eb00')
        .then(res => {            
            callback(res.data);
        });
    },
    searchMovie(callback,movieName,year){
        axios.get('https://api.themoviedb.org/3/search/movie?api_key=bd4dc10114f188e3f7c600026923eb00&query='+movieName+'&year='+year)
        .then(res => {            
            callback(res.data);
        });
    },
    getPersonDetail(callback,ID){
        axios.get('https://api.themoviedb.org/3/person/'+ID+'?api_key=bd4dc10114f188e3f7c600026923eb00')
        .then(res => {
            callback(res.data)
        });
    }
}

export default getMovieData;

